var index = require("./views/index.vue");
var catList = require("./views/categoryList.vue")
var item = require("./views/item.vue")

module.exports = function(router){
    router.map({
        "/": {
            name: 'index', //首页
            component:  index
        },
        "/index": {
            name: 'index', //首页
            component: index
        },
        "/catList": {
            name: "catList",
            component: catList
        },
        "/item" : {
            name : "item",
            component : item
        }
        //"/about": {
        //    name: "about", //关于
        //    component: about
        //},
        //"/register": {
        //    name: "register", //注册
        //    component: register
        //},
        //"/send": {
        //    name: "send", //发送
        //    component: send
        //}
    });
}





