<template>
        <div class="head">
            <div class="am-g">
                <div class="am-u-sm-2">
                    <div class="logo">
                        <img src="{{staticImg.logo}}">
                    </div>
                </div>
                <div class="am-u-sm-9">
                    <div class="search">
                        <div class="am-g">
                            <div class="am-u-sm-2">
                                <img src="{{staticImg.search_log}}" class="search_logo">
                            </div>
                            <div class="am-u-sm-10">
                                <input type="text" value="搜索你想找的商品" on-focus="if(value==defaultValue){value='';}" on-blue="if(!value){value=defaultValue;}"/>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="am-u-sm-1">
                    <div class="classification">
                        <a v-link="{path : '/catList'}">
                        <img src="{{staticImg.cat_icon}}">
                        </a>
                    </div>
                </div>
            </div>
        </div>
</template>

<script>
    module.exports = {
        data : function() {
            return {
                staticImg : {
                    logo : 'img/logo.png',
                    search_logo : 'img/search_logo.png',
                    cat_icon : 'img/classification_icon.png'
                }
            }
        }
    }
</script>




