<template>
    <div class="banner">
        <div class="am-slider am-slider-default" data-am-flexslider id="demo-slider-0">
            <ul class="am-slides">
                <li v-for="item in sliders">
                    <img src="{{item.imageUrl}}" />
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
    module.exports = {
        data : function(){
            return {
                sliders : [
                    {imageUrl:'http://s.amazeui.org/media/i/demos/bing-1.jpg'},
                    {imageUrl:'http://s.amazeui.org/media/i/demos/bing-2.jpg'},
                    {imageUrl:'http://s.amazeui.org/media/i/demos/bing-3.jpg'},
                    {imageUrl:'http://s.amazeui.org/media/i/demos/bing-4.jpg'}
                ]
            };
        }
    }
</script>


